<template>
	<view class="home">
		<swiper indicator-dots circular>
			<swiper-item v-for="item in swipers" :key="item.id">
					<image :src="item.img"></image>
				</swiper-item>
		</swiper>
		<!-- 导航区域 -->
		<view class="nav">
			<view class="nav_item" v-for="(item,index) in navs" :key="index" @click="navTtemClick(item.path)">
				<view :class="item.icon"></view>
				<text>{{item.title}}</text>
			</view>
		</view>
		<!-- 推荐商品区 -->
		<view class="hot_goods">
			<view class="tit">推荐商品</view>
			<goods-list :goods="goods"></goods-list>
		</view>
	</view>
</template>

<script>
	import goodsList from'../../components/goods-list/goods-list.vue'
	export default{
		data(){
			return{
				swipers:[],
				goods:[],
				navs:[
					{
						icon:'iconfont icon-ziyuan',
						title:'闲置物品',
						path:'/pages/goods/goods'
					},
					
					{
						icon:'iconfont icon-guanyuwomen',
						title:'联系我们',
						path:'/pages/express/express'
					},
					
					{
						icon:'iconfont icon-tupian',
						title:'校园指南',
						path:'/pages/pics/pics'
					},
					
					{
						icon:'iconfont icon-shipin',
						title:'小小老师',
						path:'/pages/teacher/teacher'
					}
				]
			}
		},
		onLoad(){
			this.getSwipers()
			this.getHotGoods()
			uni.request({
				url: this.apiServer+'index&m=index',
				method: 'GET',
				data: {},
				success: res => {
					console.log(JSON.stringify(res));
				},
				fail: (e) => {
					console.log(JSON.stringify(e));
				},
				complete: () => {}
			});
		},
		components:{"goods-list":goodsList},
		methods:{
			//获取轮播图的数据
			async getSwipers(){
				// console.log('获取轮播图数据')
				// uni.request({
				// 	url:'http://localhost:8082/api/getlunbo',
				// 	success:res=>{
				// 		console.log(res)
				// 		if(res.data.status !== 0){
				// 			return uni.showToast({
				// 				title:'获取数据失败'
				// 			})
				// 		}
				// 		this.swipers = res.data.message
				// 	}
				// })
				const res =await this.$myRuquest({
					url:'/api/getlunbo'
				})
				this.swipers = res.data.message
			},
			//获取热门商品列表数据
			async getHotGoods(){
				const res = await this.$myRuquest({
					url:'/api/getgoods?pageidex=1'
				})
				this.goods = res.data.message
			},
			//导航点击的处理函数
			navTtemClick (url) {
				// console.log(url)
				uni.navigateTo({
					url
				})
			}
		}
	}
</script>

<style lang="scss">
	.home{
		swiper{
			width: 750rpx;
			height: 380rpx;
			image{
				width: 100%;
				height: 100%;
			}
		}
		.nav{
			display: flex;
			.nav_item{
				width: 25%;
				text-align: center;
				view{
					width: 120rpx;
					height: 120rpx;
					background:$shop-color;
					border-radius:60rpx ;
					margin: 10px auto;
					line-height: 120rpx;
					color: #FFFFFF;
					font-size: 50rpx;
				}
				.icon-tupian{
					font-size: 45rpx;
				}
				text{
					font-size: 30rpx;
				}
			}
		}
		.hot_goods{
			background: #eee;
			overflow: hidden;
			margin-top: 10px;
			.tit{
				height: 50px;
				line-height: 50px;
				color: $shop-color;
				text-align: center;
				letter-spacing: 20px;
				background: #fff;
				margin: 7rpx 0rpx;
				}
				
		}
	}
</style>
